import proyectos from '~/mixins/proyectos.js'
export default {
  components: {},
  mixins: [proyectos],
  props: {
    proyecto: {
      type: Object,
      default: null,
    },
    par: {
      type: Boolean,
      default: null,
    },
  },
  methods: {
    toProject() {
      this.$router.push({
        path: '/' + this.proyecto.full_slug,
      })
    },
  },
}
